<template>
  <v-card>
    <iframe
      ref="iframe"
      src="https://form.jotform.com/212146384429053"
      id="JotFormIFrame-212146384429053"
      title="Let's connect!"
      allowtransparency="true"
      allowfullscreen="true"
      allow="geolocation; microphone; camera"
      frameborder="0"
      scrolling="no"
      @load="fadeIn($event)"
    ></iframe>
  </v-card>
</template>

<script>
export default {
  name: 'Contact',
  data() {
    return {
      ifr: null,
      iframeParams: [],
    };
  },
  created() {
    if (window.addEventListener) {
      window.addEventListener('message', this.handleIFrameMessage, false);
    } else if (window.attachEvent) {
      window.attachEvent('onmessage', this.handleIFrameMessage);
    }
  },
  mounted() {
    this.ifr = this.$refs.iframe;
    this.setup();
  },
  methods: {
    fadeIn(event) {
      console.log('fadeIn', event);
      event.currentTarget.classList.add('visible');
    },
    handleIFrameMessage(e) {
      if (typeof e.data === 'object') {
        return;
      }

      const args = e.data.split(':');
      let iframe =
        args.length > 2
          ? document.getElementById(`JotFormIFrame-${args[args.length - 1]}`)
          : document.getElementById('JotFormIFrame');

      if (!iframe) {
        return;
      }

      switch (args[0]) {
        case 'scrollIntoView':
          iframe.scrollIntoView();
          break;
        case 'setHeight':
          iframe.style.height = args[1] + 'px';
          break;
        case 'collapseErrorPage':
          if (iframe.clientHeight > window.innerHeight) {
            iframe.style.height = window.innerHeight + 'px';
          }
          break;
        case 'reloadPage':
          window.location.reload();
          break;
        case 'loadScript':
          if (!this.isPermitted(e.origin, ['jotform.com', 'jotform.pro'])) {
            break;
          }
          var src = args[1];
          if (args.length > 3) {
            src = args[1] + ':' + args[2];
          }
          var script = document.createElement('script');
          script.src = src;
          script.type = 'text/javascript';
          document.body.appendChild(script);
          break;
        case 'exitFullscreen':
          if (window.document.exitFullscreen) window.document.exitFullscreen();
          else if (window.document.mozCancelFullScreen)
            window.document.mozCancelFullScreen();
          else if (window.document.mozCancelFullscreen)
            window.document.mozCancelFullScreen();
          else if (window.document.webkitExitFullscreen)
            window.document.webkitExitFullscreen();
          else if (window.document.msExitFullscreen)
            window.document.msExitFullscreen();
          break;
      }
      const isJotForm = e.origin.indexOf('jotform') > -1 ? true : false;
      if (
        isJotForm &&
        'contentWindow' in iframe &&
        'postMessage' in iframe.contentWindow
      ) {
        var urls = {
          docurl: encodeURIComponent(document.URL),
          referrer: encodeURIComponent(document.referrer),
        };
        iframe.contentWindow.postMessage(
          JSON.stringify({ type: 'urls', value: urls }),
          '*',
        );
      }
    },
    isPermitted(originUrl, whitelisted_domains) {
      const url = document.createElement('a');
      url.href = originUrl;
      const hostname = url.hostname;
      let result = false;
      if (typeof hostname !== 'undefined') {
        whitelisted_domains.forEach(function (element) {
          if (
            hostname.slice(-1 * element.length - 1) === '.'.concat(element) ||
            hostname === element
          ) {
            result = true;
          }
        });
        return result;
      }
    },
    setup() {
      let { ifr, iframeParams } = this;
      let { src } = this.ifr;

      if (ifr) {
        if (window.location.href && window.location.href.indexOf('?') > -1) {
          iframeParams = iframeParams.concat(
            window.location.href
              .substr(window.location.href.indexOf('?') + 1)
              .split('&'),
          );
        }

        if (src && src.indexOf('?') > -1) {
          iframeParams = iframeParams.concat(
            src.substr(src.indexOf('?') + 1).split('&'),
          );
          src = src.substr(0, src.indexOf('?'));
        }

        iframeParams.push('isIframeEmbed=1');

        ifr.src = src + '?' + iframeParams.join('&');
      }
    },
  },
};
</script>

<style lang="scss">
iframe {
  min-width: 100%;
  height: 539px;
  border: none;
  transition: opacity 0.5s linear;
  opacity: 0;

  &.visible {
    opacity: 1;
  }
}
</style>
